import React, { Fragment, useState } from "react";
import { login } from "../actions/users";
import Footer from "./Footer";
import LoginModal from "./modals/LoginModal";
import Navbar from "./Navbar";
import { connect, useDispatch } from "react-redux";
import Loader from "./Loader";
import { SET_LOADING } from "../actions/types";
import { useDarkMode } from '../utils/themes/useDarkMode'
import { GlobalStyles, lightTheme, darkTheme} from '../utils/themes/theme'
import {ThemeProvider} from 'styled-components'

const Layout = ({ children, auth }) => {
  const [username, setUsername] = useState('');
  const [isShow, setIsShow] = useState(false);
  const [loading, setLoading] = useState(false);
   const [theme, toggleTheme] = useDarkMode();
   const themeMode = theme === 'light' ? lightTheme : darkTheme;
  const dispatch = useDispatch();
  const setUsernameField = (e) => {
    setUsername(e.target.value);
  };

  const loginUser = () => {
    dispatch({ type: SET_LOADING, payload: true });
    dispatch(login(username));
    setIsShow(false);
  };
  const hideModal = () => {
    setIsShow(false);
  };

  return (
    <Fragment>
      <ThemeProvider theme={themeMode}>
        <GlobalStyles />
        <Navbar
          isShowLogIn={setIsShow}
          isShow={isShow}
          loading={loading}
          setLoading={setLoading}
          toggleTheme={toggleTheme}
        />
        {auth.loading ? <Loader /> : <main>{children}</main>}
        <LoginModal
          show={isShow}
          handleClose={hideModal}
          username={username}
          setUsernameField={setUsernameField}
          loginUser={loginUser}
        />
        <Footer />
      </ThemeProvider>
    </Fragment>
  );
};
const mapStateToProps = (state) => ({
  auth: state.users,
});
export default connect(mapStateToProps, {})(Layout);
