import React, { Fragment, useEffect } from 'react';
import { connect } from 'react-redux';
import { getArtisteProfile } from '../actions/profile';

const ArtisteProfileHeader = ({
  artiste_profile,
  getArtisteProfile,
  param,
}) => {
  useEffect(() => {
    getArtisteProfile(param);
  }, [getArtisteProfile, param]);
  return (
    <Fragment>
      <section
        className='nfttunz__main__header'
        style={{ marginBottom: '0px' }}
      >
        <div className='nfttunz__profile__header__overlay'>
          <div className='container'>
            <div className='row'>
              <div className='col-md-12'>
                <div className='profile__header__title text-center'>
                  <img
                    src={
                      artiste_profile?.avatar
                        ? artiste_profile?.avatar
                        : 'https://cdn.pixabay.com/photo/2016/05/24/22/54/icon-1413583_960_720.png'
                    }
                    alt='user avatar'
                    width={100}
                  />
                  <h1>
                    {artiste_profile?.full_name
                      ? artiste_profile?.full_name
                      : 'TUNZ User'}
                  </h1>
                  <small>
                    <a
                      href={`https://www.hive.blog/@${artiste_profile?.username}`}
                      target='_blank'
                      rel='noreferrer'
                    >
                      @{param},{' '}
                      {artiste_profile?.location
                        ? artiste_profile?.location
                        : 'nfttunz'}
                    </a>
                  </small>
                </div>
                <div className='profile__header__icons text-center'>
                  <a
                    href={artiste_profile?.twitter}
                    target='_blank'
                    rel='noreferrer'
                  >
                    <i className='fab fa-twitter'></i>
                  </a>
                  <a
                    href={artiste_profile?.instagram}
                    target='_blank'
                    rel='noreferrer'
                  >
                    <i className='fab fa-instagram'></i>
                  </a>
                  <a
                    href={artiste_profile?.soundcloud}
                    target='_blank'
                    rel='noreferrer'
                  >
                    <i className='fab fa-soundcloud'></i>
                  </a>
                  <a
                    href={artiste_profile?.website}
                    target='_blank'
                    rel='noreferrer'
                  >
                    <i className='fas fa-globe'></i>
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </Fragment>
  );
};
const mapStateToProps = (state) => ({
  artiste_profile: state.profiles.artiste_profile,
});
export default connect(mapStateToProps, { getArtisteProfile })(ArtisteProfileHeader);
