import {
  SET_PROFILE,
  GET_PROFILE,
  CLEAR_PROFILE,
  GET_ARTISTE_PROFILE,
} from '../actions/types';

const intialState = {
  profile: {
    full_name: 'TUNZ user',
    avatar: '',
    bio: '',
  },
  artiste_profile: {},
  loading: true,
};

const profileReducer = (state = intialState, action) => {
  const { type, payload } = action;

  switch (type) {
    case SET_PROFILE:
      return {
        ...state,
        profile: { ...payload },
        loading: false,
      };

    case GET_PROFILE:
      return {
        ...state,
        profile: { ...payload },
        loading: false,
      };
    case GET_ARTISTE_PROFILE:
      return {
        ...state,
        artiste_profile: {...payload},
        loading: false,
      };
    case CLEAR_PROFILE:
      return {
        ...state,
        profile: null,
        loading: false,
      };
    default:
      return state;
  }
};

export default profileReducer;
